@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;600&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  outline: none;
}

a {
  text-decoration: none;
  color: lightgray;
}

body {
  font-family: 'Rubik', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: white;
  max-width: 1100px;
  margin: 0 auto;

}
 
.App{padding:20px;text-align:left}.won-card{background-color:#4da375}.lost-card{background-color:#a34d5d}

.MatchDetailCard{display:grid;grid-template-columns:repeat(2, 1fr);padding:10px;margin-bottom:20px}.MatchDetailCard .match-date,.MatchDetailCard .match-venue,.MatchDetailCard .match-result{margin-top:10px}.MatchDetailCard .additional-detail{text-align:right}.MatchDetailCard .additional-detail>p{margin-bottom:20px}

.MatchSmallCard{padding:10px;margin-bottom:20px}.MatchSmallCard .match-result{margin-top:10px}

.TeamPage{display:grid;grid-template-columns:repeat(4, 1fr);grid-template-rows:repeat(3, 1fr);grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px;grid-row-gap:10px;row-gap:10px}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:end}.TeamPage .team-name{font-size:4rem;margin-bottom:20px}.TeamPage .win-loss-section{justify-self:center;text-align:center;padding:40px}.TeamPage .match-detail-section{grid-column:span 4}.TeamPage .more-link{font-size:1.5rem;-webkit-align-self:center;align-self:center;justify-self:center}

.YearSelector{list-style-type:none}.YearSelector li{margin:10px 0}

.MatchPage{display:grid;grid-template-columns:1fr 5fr;grid-column-gap:20px;-webkit-column-gap:20px;column-gap:20px}.MatchPage .year-selector{justify-self:center;margin-top:1rem}.MatchPage .heading{display:grid;grid-template-columns:4fr 1fr;padding:10px 0}.MatchPage .heading>h4{-webkit-align-self:center;align-self:center;justify-self:end}

.TeamTile{height:170px;text-align:center;border:1px solid white;display:grid;-webkit-align-items:center;align-items:center;border-radius:5px}

.HomePage .team-grid{display:grid;grid-template-columns:repeat(2, 1fr);grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px;grid-row-gap:10px;row-gap:10px}.HomePage .app-name{font-size:3rem;text-align:center;margin:30px auto}

